/* Base Styles
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  --fi-blue: #384ca2;
  --fi-grey: #807f84;
  --fi-teal: #00839b;
  --fi-green: #4c9d2f;
  --fi-yellow: #f6cf3f;
  --fi-brown: #903f2c;
  --fi-orange: #f28b00;
  --liz-lilac: #b96ac9;
  --black: #2a2a2a;
  --off-black: #222;
  --off-white: #fafafd;
  --dark-grey: #282c34;
  --label-grey: #6b6b76;
  --sans: "Quattrocento Sans", sans-serif;
  --serif: "Quattrocento", serif;
  --mono: "Rajdhani", sans-serif;
}
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5em; /* currently ems cause chrome bug misinterpreting rems on body element */
  line-height: 1.6;
  font-weight: 400;
  font-family: "Quattrocento Sans", sans-serif;
  font-family: var(--sans);
  color: #2a2a2a;
  color: var(--black);
}

.wrapper {
  min-height: 100vh;
  position: relative;
  background-color: white;
}

/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: 300;
  font-family: "Quattrocento Sans", sans-serif;
  font-family: var(--sans);
}
h1 {
  font-size: 4rem;
  line-height: 1.2;
  letter-spacing: -0.1rem;
}
h2 {
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -0.1rem;
}
h3 {
  font-size: 3rem;
  line-height: 1.3;
  letter-spacing: -0.1rem;
}
h4 {
  font-size: 2.4rem;
  line-height: 1.35;
  letter-spacing: -0.08rem;
}
h5 {
  font-size: 1.8rem;
  line-height: 1.5;
  letter-spacing: -0.05rem;
}
h6 {
  font-size: 1.5rem;
  line-height: 1.6;
  letter-spacing: 0;
}

/* Larger than phablet */
@media (min-width: 550px) {
  h1 {
    font-size: 5rem;
  }
  h2 {
    font-size: 4.2rem;
  }
  h3 {
    font-size: 3.6rem;
  }
  h4 {
    font-size: 3rem;
  }
  h5 {
    font-size: 2.4rem;
  }
  h6 {
    font-size: 1.5rem;
  }
}

p {
  margin-top: 0;
  font-family: "Quattrocento Sans", sans-serif;
  font-family: var(--sans);
}

code {
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
  font-size: 85%;
  margin: 0;
  padding: 0.2em 0.4em;
  color: #282c34;
  color: var(--dark-grey);
}

/* Links
–––––––––––––––––––––––––––––––––––––––––––––––––– */
a {
  border-bottom: 1px solid #384ca2;
  border-bottom: 1px solid var(--fi-blue);
  font-family: "Quattrocento Sans", sans-serif;
  font-family: var(--sans);
  color: #2a2a2a;
  color: var(--black);
}
a:hover {
  text-decoration: none;
  color: #384ca2;
  color: var(--fi-blue);
}

/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  display: inline-block;
  height: 38px;
  padding: 0 15px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
}
.button:hover,
button:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
.button:focus,
button:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="button"]:focus {
  color: #333;
  border-color: #888;
  outline: 0;
}
.button.button-primary,
button.button-primary,
input[type="submit"].button-primary,
input[type="reset"].button-primary,
input[type="button"].button-primary {
  color: #fff;
  background-color: #384ca2;
  background-color: var(--fi-blue);
  border-color: #384ca2;
  border-color: var(--fi-blue);
}
.button.button-primary:hover,
button.button-primary:hover,
input[type="submit"].button-primary:hover,
input[type="reset"].button-primary:hover,
input[type="button"].button-primary:hover,
.button.button-primary:focus,
button.button-primary:focus,
input[type="submit"].button-primary:focus,
input[type="reset"].button-primary:focus,
input[type="button"].button-primary:focus {
  color: #fff;
  background-color: #f6cf3f;
  background-color: var(--fi-yellow);
  border-color: #f6cf3f;
  border-color: var(--fi-yellow);
}

