/* Base Styles
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  --fi-blue: #384ca2;
  --fi-grey: #807f84;
  --fi-teal: #00839b;
  --fi-green: #4c9d2f;
  --fi-yellow: #f6cf3f;
  --fi-brown: #903f2c;
  --fi-orange: #f28b00;
  --liz-lilac: #b96ac9;
  --black: #2a2a2a;
  --off-black: #222;
  --off-white: #fafafd;
  --dark-grey: #282c34;
  --label-grey: #6b6b76;
  --sans: "Quattrocento Sans", sans-serif;
  --serif: "Quattrocento", serif;
  --mono: "Rajdhani", sans-serif;
}
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5em; /* currently ems cause chrome bug misinterpreting rems on body element */
  line-height: 1.6;
  font-weight: 400;
  font-family: var(--sans);
  color: var(--black);
}

.wrapper {
  min-height: 100vh;
  position: relative;
  background-color: white;
}

/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: 300;
  font-family: var(--sans);
}
h1 {
  font-size: 4rem;
  line-height: 1.2;
  letter-spacing: -0.1rem;
}
h2 {
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -0.1rem;
}
h3 {
  font-size: 3rem;
  line-height: 1.3;
  letter-spacing: -0.1rem;
}
h4 {
  font-size: 2.4rem;
  line-height: 1.35;
  letter-spacing: -0.08rem;
}
h5 {
  font-size: 1.8rem;
  line-height: 1.5;
  letter-spacing: -0.05rem;
}
h6 {
  font-size: 1.5rem;
  line-height: 1.6;
  letter-spacing: 0;
}

/* Larger than phablet */
@media (min-width: 550px) {
  h1 {
    font-size: 5rem;
  }
  h2 {
    font-size: 4.2rem;
  }
  h3 {
    font-size: 3.6rem;
  }
  h4 {
    font-size: 3rem;
  }
  h5 {
    font-size: 2.4rem;
  }
  h6 {
    font-size: 1.5rem;
  }
}

p {
  margin-top: 0;
  font-family: var(--sans);
}

code {
  background-color: rgba(27, 31, 35, 0.05);
  border-radius: 3px;
  font-size: 85%;
  margin: 0;
  padding: 0.2em 0.4em;
  color: var(--dark-grey);
}

/* Links
–––––––––––––––––––––––––––––––––––––––––––––––––– */
a {
  border-bottom: 1px solid var(--fi-blue);
  font-family: var(--sans);
  color: var(--black);
}
a:hover {
  text-decoration: none;
  color: var(--fi-blue);
}

/* Buttons
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  display: inline-block;
  height: 38px;
  padding: 0 15px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
}
.button:hover,
button:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
.button:focus,
button:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="button"]:focus {
  color: #333;
  border-color: #888;
  outline: 0;
}
.button.button-primary,
button.button-primary,
input[type="submit"].button-primary,
input[type="reset"].button-primary,
input[type="button"].button-primary {
  color: #fff;
  background-color: var(--fi-blue);
  border-color: var(--fi-blue);
}
.button.button-primary:hover,
button.button-primary:hover,
input[type="submit"].button-primary:hover,
input[type="reset"].button-primary:hover,
input[type="button"].button-primary:hover,
.button.button-primary:focus,
button.button-primary:focus,
input[type="submit"].button-primary:focus,
input[type="reset"].button-primary:focus,
input[type="button"].button-primary:focus {
  color: #fff;
  background-color: var(--fi-yellow);
  border-color: var(--fi-yellow);
}

/* Containers
–––––––––––––––––––––––––––––––––––––––––––––––––– */

/* NOTE
Use containers when you need a content area that has standard maximum width and centered on the page.
<div class="container {modifier_class}">Container</div>
*/
.app {
  background-color: var(--fi-grey);
}
.container {
  margin: 0 auto;
  padding: 0 10px;
  width: 95%;
  box-sizing: border-box;
}

.col {
  width: 100%;
  float: left;
  box-sizing: border-box;
}

@media (min-width: 400px) {
  .container {
    width: 85%;
    padding: 0;
  }
}

@media (min-width: 550px) {
  .container {
    width: 95%;
  }
  .col {
    margin-left: 2%;
  }
  .col:first-child {
    margin-left: 0;
  }
  .col--1 {
    width: 6.5%;
  }
  .col--2 {
    width: 15%;
  }
  .col--3 {
    width: 23.5%;
  }
  .col--4 {
    width: 32%;
  }
  .col--5 {
    width: 40.5%;
  }
  .col--6 {
    width: 49%;
  }
  .col--7 {
    width: 57.5%;
  }
  .col--8 {
    width: 66%;
  }
  .col--9 {
    width: 74.5%;
  }
  .col--10 {
    width: 83%;
  }
  .col--11 {
    width: 91.5%;
  }
  .col--12 {
    width: 100%;
    margin-left: 0;
  }
  .col--1-3 {
    width: 32%;
  }
  .col--2-3 {
    width: 66%;
  }
  .col--1-2 {
    width: 49%;
  }
  .col--offset-1 {
    margin-left: 8.5% !important;
  }
  .col--offset-2 {
    margin-left: 17% !important;
  }
  .col--offset-3 {
    margin-left: 25.5% !important;
  }
  .col--offset-4 {
    margin-left: 34% !important;
  }
  .col--offset-5 {
    margin-left: 42.5% !important;
  }
  .col--offset-6 {
    margin-left: 51% !important;
  }
  .col--offset-7 {
    margin-left: 59.5% !important;
  }
  .col--offset-8 {
    margin-left: 68% !important;
  }
  .col--offset-9 {
    margin-left: 76.5% !important;
  }
  .col--offset-10 {
    margin-left: 85% !important;
  }
  .col--offset-11 {
    margin-left: 93.5% !important;
  }
  .col--offset-1-3 {
    margin-left: 34% !important;
  }
  .col--offset-2-3 {
    margin-left: 68% !important;
  }
  .col--offset-1-2 {
    margin-left: 51% !important;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1480px;
  }
}

.container__body {
  padding: 7.2rem 1rem;
}

.container:after,
.row:after {
  clear: both;
  content: "";
  display: table;
}

.container__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.container__left-menu {
  width: 100%;
}

.container__error {
  margin-top: 10rem;
}

.container__algos {
  margin-bottom: 7.2rem;
}

.container__body--algos {
  padding: 7.2rem 1rem 0rem 1rem;
}

/* Lists
–––––––––––––––––––––––––––––––––––––––––––––––––– */
ul {
  list-style: circle inside;
}
ol {
  list-style: decimal inside;
}
ol,
ul {
  padding-left: 0;
  margin-top: 0;
}
ul ul,
ul ol,
ol ol,
ol ul {
  margin: 1.5rem 0 1.5rem 3rem;
  font-size: 90%;
}
li {
  margin-bottom: 1rem;
}

/* Recordings
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.card {
  position: static;
}

/* Errors
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.errors {
  background-color: white;
  max-width: 780px;
  margin: 3rem auto;
  padding: 1.8rem;
  font-size: 1.8rem;
  line-height: 2.9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.errors__message {
  color: var(--fi-brown);
  margin-block-end: 0;
}
.errors__close {
  cursor: pointer;
  margin-block-end: 0;
  font-family: var(--sans);
}
.errors__close:hover {
  color: var(--fi-blue);
}

/* Algorithms
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.algos {
  font-family: var(--sans);
}

/* React Collapsible Table
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.react-collapsible-theme {
  text-align: left;
  padding: 2rem;
  background-color: #fff;
}

/* Heatmap
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.heatmap__col {
  height: auto;
  padding-bottom: 150px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.rv-continuous-color-legend .rv-gradient {
  height: 12px !important;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.2) !important;
}

.rv-legend-titles {
  font-weight: bold !important;
}

.slider__title {
  font-family: var(--serif);
}

.rv-xy-plot__series--label-text {
  font-size: 20px;
  font-weight: bold;
  cursor: default !important;
}

.rv-xy-plot__axis__title {
  transform: translate(-163, 35) !important;
  font-weight: bold !important;
}

.rv-xy-plot__series--heatmap > rect:hover {
  stroke-width: 1 !important;
  stroke: #f6782d !important;
}

/* Legend
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.legend__container {
  max-height: 500px;
}
.legend__title {
  font-family: var(--serif);
}

.legend__copy {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-align: left;
  margin-bottom: 3rem;
}
